import BindHeader from '@/bind/components/header'
import BindForm from '@/bind/components/form'
export default {
  name: 'index',
  components: {
    BindHeader,
    BindForm
  },
  methods: {
    judge () {
      if (localStorage.getItem('id')) alert('您已经绑定过了！')
    }
  },
  created () {
    this.judge()
  }
}



