export default {
  name: 'BindForm',
  data () {
    return {
      item: {
        name: '',
        telephone: '',
        id_card: '',
        level: Number,
        code: this.$global.getUrlKey('code')
      },
      level: [
        {
          value: 6,
          label: '最高负责人'
        },
        {
          value: 5,
          label: '老师'
        },
        {
          value: 4,
          label: '助理'
        },
        {
          value: 3,
          label: '学生'
        },
        {
          value: 2,
          label: '专家教授'
        }
      ]
    }
  },
  methods: {
    submit () {
      this.$axios({
        method: 'post',
        url: this.url + '/user/',
        data: JSON.stringify(this.item)
      }).then((user) => {
        alert('绑定成功')
        const data = user.data
        console.log(data)
        localStorage.setItem('level', data.level)
        localStorage.setItem('id', data.id)
        localStorage.setItem('name', data.name)
        console.log(localStorage.getItem('id'))
        this.$router.push('/project')
      }).catch((err) => {
        alert(err)
      })
    }
  },
  created () {
    this.item.level = 1
  }
}



